<template>
    <div class="total-wrapper">
        <div class="total-card br-16 bg-white flx jc-sb gap-24">
            <div class="flx column">
                <div class="flx gap-8 ai-c">
                    <div class="fs-105rem">${{ computePrice(computedTotal) }}</div>
                    <div v-if="!computedTotal == 0" class="flx gap-8">
                        <span class="gray">or</span>
                        <div class="gray-2">${{ computePrice(computedTotal/12) }}/Mon</div>
                    </div>
                </div>
                <div class="gray">Estimated cost</div>
            </div>
            <div v-if="button" class="centered">
                <button @click="confirm" class="button-primary br-32 scale-in confirm-btn" :class="{ 'button-disabled' : !allChecked }" :disabled="!allChecked ? true : false">
                    {{ user.address ? 'Continue' : 'Confirm availability' }}
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import totalMixin from '@/mixins/totalMixin'
import priceMixin from '@/mixins/priceMixin';
export default {
    name: 'TotalCard',
    mixins: [priceMixin, totalMixin],
    props: ['button'],
    computed: {
        allChecked() {
            const config = this.configuration
            if(config.type.name && config.color.name && 
            config.appliance.name && config.exterior.name && 
            config.sewage.name && config.blind.name )
            return true
            else
            return false
        }
    },
    methods: {
        confirm() {
            this.$router.push({ name: 'ConfigureAddress'})
        }
    }
}
</script>

<style lang="scss" scoped>
.total-wrapper{
    container-type: inline-size;
}
@container(inline-size <= 460px) {
    .total-card {
        flex-direction: column
    }
    .centered {
        place-items: stretch
    }
}
</style>